<template>
  <KeepAlive> </KeepAlive>
  <router-view></router-view>
</template>

<script setup>
import router from "@/router";
import { defineComponent, provide, ref } from "vue";
import { getQueryString } from "@/utlis/QueryStr";
import { useI18n } from "vue-i18n";
const { locale } = useI18n();

//   -------------------
/*
  : 
  1.  [   ]
  2.  [   ]
  3. locale :     : false ===   true === , 
  4. guide :   true ===   false === 
  5. [  ]
*/
const model = true; //  true  false
const lang = ref(true); //  false ===   true === 

// ---------------------------------
console.log(window.location.href);
// alert()
// locale.value = "zh";
try {
  // 
  if (model) {
    var hash = getQueryString("hash");
    router.push("/" + hash); // 

    locale.value = getQueryString("la"); // 
    lang.value = locale.value == "zh" ? true : false; // true     false 
  } else {
    // 
    locale.value = "zh"; // 
    lang.value = true;
    var route = getQueryString("hash");
    if (route == undefined) {
      router.push("/");
    } else {
      // router.push("/" + route);
      router.push("/PreviewOverlay");
    }

    // router.push("/AlarmIoInfo"); // 
  }
  var modele = getQueryString("model");
  sessionStorage.model = modele;
} catch (e) {
  console.warn(e);
}

// 
provide("lang", { lang });

defineComponent({
  name: "app",
});
</script>

